import React, { Component } from 'react'
import { Row, Col, Container } from 'react-bootstrap'

import '../common/styles.css'

class Articles extends Component {
    constructor(props) {
        super(props)
        this.state={}
    }

    render() {
        return (
            <Container className={Row} style={{marginTop: "50px"}}>
                <Row>
                    <Col sm="1" md="2"></Col>
                    <Col xs="12" sm="10" md="8">
                        <h1>Blog</h1>
                        <ArticleList></ArticleList>
                    </Col>
                    <Col sm="1" md="2"></Col>
                </Row>
            </Container>
        )
    }
}

class ArticleList extends Component {
    constructor(props) {
        super(props)
        this.list = [
            {
                id: 'first-article',
                title: 'Primul articol',
                date: 'Mar 27, 2020'
            },
        ]
    }

    render() {
        return (
            <div>
                {this.list.map(item => (
                    <ArticleItem key={item.title} id={item.id} title={item.title} date={item.date}></ArticleItem>
                ))}
            </div>
        )
    }
}

class ArticleItem extends Component {
    constructor(props) {
        super(props)

        this.id = props.id
        this.date = props.date
        this.title = props.title
    }

    handleClick() {
        window.location.href = "/#/article/" + this.id;
    }

    render() {
        return (
            <div className="shadowed" onClick={() => this.handleClick()} style={{padding: "15px 15px 15px 15px", marginTop: "30px"}}>
                <h3>{this.title}</h3>
                <span style={{float: "right"}}>{this.date}</span><br></br>
            </div>
        )
    }
}

export default Articles